* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  position: absolute;
  inset: 0;
  height: 100%;
  width: 100%;
  background-color: white;
  font-family: "Work Sans", sans-serif;
  transition: all 0.3s ease;
  overflow: hidden;
}

.login-page {
  display: flex;
}
.login-page .lendsqr-logo {
  height: 36px;
}
.login-page .images {
  display: flex;
  align-items: center;
  justify-content: center;
}
.login-page .form-container {
  box-shadow: 0px 15px 90px rgba(0, 0, 0, 0.03);
  display: flex;
  align-items: center;
}
.login-page form {
  width: 450px;
  display: flex;
  flex-direction: column;
  padding-left: 100px;
}
.login-page form h1 {
  color: #213F7D;
  font-size: 40px;
  font-weight: 700;
  line-height: 55px;
}
.login-page form h3 {
  margin-bottom: 50px;
  color: #545F7D;
  font-size: 20px;
  font-weight: 400;
  line-height: 28px;
}
.login-page form input {
  height: 50px;
  width: 100%;
  padding: 0 15px;
  margin-bottom: 20px;
  outline: none;
  border: 2px solid rgba(84, 95, 125, 0.15);
  border-radius: 5px;
  color: #545F7D;
  font-size: 14px;
  line-height: 20px;
}
.login-page form input:focus {
  border-color: #39CDCC;
}
.login-page form .password-input {
  position: relative;
}
.login-page form .password-input span {
  position: absolute;
  right: 15px;
  line-height: 50px;
}
.login-page form span {
  color: #39CDCC;
  font-size: 12px;
  font-weight: 600;
  line-height: 16.39px;
  cursor: pointer;
}
.login-page form button {
  background: #39CDCC;
  border-radius: 8px;
  color: white;
  font-size: 14px;
  font-weight: 600;
  line-height: 20px;
  text-align: center;
  cursor: pointer;
  height: 48px;
  width: 100%;
  margin-top: 30px;
  border: none;
}